.header {
  margin-top: 20px;
}

.wrapper-header {
  position: relative;
}

.wrapper-name {
  height: 200px;
  background-color: var(--background-color-header);
  padding: 40px;
}

.name {
  font-weight: 400;
  font-size: 30px;
  line-height: 1.3;
  text-align: center;
  color: #f7f7f7;
}
.wrapper-profile {
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
}

.wrapper-profile__img {
  display: block;
  width: 100%;
  height: 100%;
}

.profession {
  margin-top: 100px;
  text-align: center;
}

//

.wrapper-contacts {
  margin-top: 5px;
}
.contacts-list {
  display: flex;
  justify-content: center;
  list-style: none;
}

.contacts-list__link {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  fill: var(--background-inactive-icon-color);
  border-radius: 50%;
  background-color: inherit;

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    fill: var(--inactive-color-icon-text);
    cursor: pointer;
    border-radius: 50%;
    background-color: var(--background-accent-icon-color);
  }
}

.about-me {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.4;
  text-align: center;
  margin-top: 15px;
}
